.headerContainer {
    position: fixed;
    top: 0;
    width: 100%;
    height: 50px;
    background: white;
    border-width: 0 0 1px 0;
    border-style: solid;
    border-color: black;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
    z-index: 1;
}

.headerLogo {
    margin: 0 25px;;
    font-size: medium;
    justify-self: center;
}

.headerSignInOrOut {
    margin: 0 10px;
    font-size: medium;
}

.headerNavContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-right: 20px;
}

.headerLinksContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.headerLink {
    margin-right: 25px;
    color: black;
}

.headerProfilePicture {
    border-radius: 80px;
    height: 40px;
    cursor: pointer;
}

.headerPlaceholder {
    width: 50px;
    margin: 0 10px;
}

.headerProfileDropdownContainer {
    position: absolute;
    right: 0;
    top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.headerProfileDropdown {
    position: absolute;
    right: 10px;
    top: 46px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    background: white;
    border-radius: 5px;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
    margin-top: 5px;
    list-style-type: none;
    cursor: pointer;
    overflow: hidden;
}

.headerProfileDropdown li {
    padding: 10px;
    width: 100%;
}

.headerProfileDropdown li:hover {
    background: rgb(241, 241, 241);
}